
#container{padding:1rem 1.2rem;margin:0 auto;height:80%;text-align:center
}
#container .search-input{box-sizing:border-box;margin:0 auto;padding:.8rem 1.2rem;width:80%;font-size:1.2rem;font-family:cursive,"SimHei",sans-serif;color:#000;border:none;border-radius:15px;line-height:1rem;transition:ease-in-out .4s
}
#container .search-input:active,#container .search-input:focus,#container .search-input.active{width:100%;border-radius:0;outline:none
}
#container .info{color:#fff;letter-spacing:.05em;opacity:.8
}
#container .trans-button{padding:.6rem 1.2rem;background-color:#fff;border:none;font-size:1.2rem;line-height:1.2rem;font-family:cursive,"SimHei",sans-serif;border-radius:5px
}


