
html,body{margin:0 0;padding:0 0;width:100%;height:100%;font-family:cursive,"SimHei",sans-serif;font-size:14px;overflow:hidden
}
html #app,body #app{width:100%;height:100%;background:#1f8dd6;overflow-x:hidden;overflow-y:auto
}
html #app .app-container,body #app .app-container{width:auto;height:100%
}
@media screen and (min-width:1024px){
html,body{width:100%;height:100%;background:linear-gradient(to right bottom, #fff, #1f8dd6)
}
html #app,body #app{width:700px;height:90%;margin:2.5% auto;border-radius:5px;box-shadow:0 0 10px #888
}
}


